* {
    box-sizing: border-box;
}

html {
    font-family: sans-serif;
    font-weight: 600;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

button {
    border: unset;
    background: unset;
    padding: 0;
    outline: none;
    // border: 1px solid black;
    color: black;
    border-radius: 5px;
    font-size: 1rem;
}
button:hover {
    cursor: pointer;
}
// button:active {
//     color: white;
//     border-color: white;
// }

input,
textarea {
    width: 200px;
    outline: none;
}

input,
select {
    padding: 5px;
    padding-left: none;
    // border: unset;
    // background: unset;
    // border-bottom: 1px solid black;
}

textarea {
    height: 75px;
}

a,
.router-link {
    color: unset;
    text-decoration: none;
}

header {
    padding: 5px;
}
